.todo {
  list-style: none;
  position: relative;
  padding: 0 50px;
  line-height: 45px;
  font-size: 20px;
}

.todo:not(:last-child):after {
  content: '';
  position: absolute;
  left: 5%;
  bottom: 0;
  width: 90%;
  height: 1px;
  background: #f2f2f2;
}

.checkbox {
  position: absolute;
  left: 10px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 25px;
  height: 25px;
  border: 1px solid #dce0e3;
  border-radius: 5px;
  cursor: pointer;
}

.checked:after {
  content: '\2714';
  position: absolute;
  width: 100%;
  height: 100%;
  font-size: 16px;
  text-align: center;
  line-height: 25px;
  border-radius: 5px;
  color: #fff;
  background-color: #4395ff;
}

.delete {
  position: absolute;
  right: 10px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  font-size: 20px;
  cursor: pointer;
}

.complete {
  color: #999999;
  text-decoration: line-through;
}

* {
  margin: 0;
  padding: 0;
}

html,
body {
  background-image: url(../../static/media/bg.aedce00e.jpg);
}

.container {
  width: 500px;
  margin: 0 auto;
  font-family: cursive;
}

header {
  text-align: center;
  font-size: 36px;
  font-weight: 500;
  line-height: 1.1;
  color: #777;
  margin: 30px 0;
}

.new-todo {
  box-sizing: border-box;
  width: 100%;
  border-radius: 5px;
  outline: none;
  border: 1px solid #cccccc;
  padding: 12px 10px;
  font-size: 14px;
  transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
}

.new-todo:focus {
  border-color: #66afe9;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 8px rgba(102, 175, 233, .6);
}

section {
  margin: 15px 0;
  padding: 10px;
  text-align: center;
  border: 1px solid #eee;
  border-left-width: 5px;
  border-radius: 3px;
  border-left-color: #d9534f;
  background-color: #fff;
}

section span {
  display: inline-block;
  margin: 0 5px;
}

section span:nth-child(n+2) {
  cursor: pointer;
}

section .on {
  color: #4395ff;
}

section .wait {
  float: left;
}

section .clear {
  float: right;
}

.todo-list {
  background: #fff;
}

